}

body {
  background-color:#FFFFFF;
  display: flex;
  flex-direction: column;
  color: black;
  font-family: 'Quicksand';
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
}

  .page-title {
    font-family: 'Quicksand';
    font-weight: 800;
    font-size: 60px;
    line-height: 82px;
    color: #333333;
    /* margin: 80px 20px 20px 20px; */
    margin: 80px 80px 20px;
  }

  .cover-title {
    margin-top: 40px;
    color: white;
  }

  .section-title {
    font-family: 'Quicksand';
    font-weight: bold;
    font-size: 36px;
    line-height: 24px;
  }

.impact-button {
  cursor: pointer;
  font-family: 'Quicksand';
  font-weight: bold;
  color: white;
  border: 1px dotted #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
  font-family: 'Quicksand';
  outline: none;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  background: #BB6BD9;
  /* border-radius: 8px; */
  border-radius: 4px;
  padding: 5px 10px 5px 10px;
}

  .impact-button-impactpage {
    /* color: white; */
    background: #9B51E0;
    /* border: 1px dotted #FFFFFF;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    padding: 5px 10px 5px 10px; */
  }

  .unit-text {
    display: inline;
  }

button:hover {
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

a:link, a:visited, a:active {
  text-decoration: none;
  color: #4F4F4F;
  font-weight: normal;
}

a:hover {
  /* text-decoration: underline; */
  color: #4F4F4F;
}

